
a {
  color: #42b883;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
pre {
  max-width: 600px;
  margin: 20px auto;
}
header h1 {
  letter-spacing: .01em;
  font-size: 3.4vmax;
  font-weight: lighter;
}
header h1 a {
    font-weight: bold;
}
footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 0;
}
main {
  max-width: 600px;
  margin: 0 auto;
}
main img {
    max-height: 400px;
    min-width: 600px;
}
@media (max-width: 40rem) {
main img {
        min-width: 100%;
}
}
@media (max-width: 24rem) {
footer {
    zoom: .7;
}
}



